import React from 'react'
import Lunbo from './Lunbo'
import { BrowserRouter as Router, Redirect, Route, NavLink, Switch } from 'react-router-dom'
import { SearchBar, Button, WhiteSpace, WingBlank } from 'antd-mobile';
import Mytab from './Mytab';
import Commodity from './Commodity';

// iconfont.cn官网搜选图标，加入购物车
// 点击页面右上角购物车，将图标添加进项目（没有项目就新建）
// 点击“下载到本地”
// 解压压缩包，全部复制到项目public文件夹下，
// 在index.html中通过link引入iconfont.css
// 使用时，<i className='iconfont icon-具体名字'></i>

const Home = (props) => {
    return (
        <div>
            <NavLink to='/home/lookfor'>
            <SearchBar 
            onClick={()=>props.history.push('/home/lookfor')}
            placeholder='寻找宝贝店铺' /></NavLink>
            <Lunbo />
            <Commodity />
            <Mytab />
        </div>
    )
}

export default Home 