import React from 'react'
import { Button, TabBar } from 'antd-mobile';

const Jump = (props) => {
    return (
        <div>
            <span onClick={()=>{
                props.history.goBack('/Home')
            }}>{`<=返回`}</span>
            <img src='https://img.alicdn.com/bao/uploaded/i4/544323876/O1CN01pu4M0G1eVFhUvfYKD_!!544323876.jpg_468x468q75.jpg_.webp'/>
            <TabBar
                    unselectedTintColor="#949494"
                    tintColor="red"
                    barTintColor="white">
                    <TabBar.Item
                        title="店铺"
                        key="dianpu"
                        icon={
                            <i className='iconfont iconwarehouse'></i>                           
                        }
                        selectedIcon={
                            <i className='iconfont iconwarehouse'></i>                           
                        }
                        onPress={ () => {
                            props.history.push('/Dianpu');
                        } }
                        data-seed="logId">
                    </TabBar.Item>
                    <TabBar.Item
                        icon={
                            <i className='iconfont iconcart-Empty'></i>                           
                        }
                        selectedIcon={
                            <i className='iconfont iconcart-Empty'></i>                           
                        }
                        title="客服"
                        key="cart"
                        onPress={ () => {
                            props.history.push('/dianpu')
                        } }
                        data-seed="logId1">
                    </TabBar.Item>
                    <TabBar.Item
                        icon={
                            <i className='iconfont iconcopy'></i>
                        }
                        selectedIcon={
                            <i className='iconfont iconcopy'></i>
                        }
                        title="收藏"
                        key="order"
                        onPress={ () => {
                            props.history.push('/dianpu')
                        } }
                        data-seed="logId2">
                    </TabBar.Item>
                    <TabBar.Item
                        icon={<i className='iconfont iconusercenter'></i> }
                        selectedIcon={<i className='iconfont iconusercenter'></i> }
                        title="加入购物车"
                        key="taobao"
                        onPress={ () => {
                            props.history.push('/dianpu')
                        } }>      
			        </TabBar.Item>
                    <TabBar.Item
                        icon={<i className='iconfont iconusercenter'></i> }
                        selectedIcon={<i className='iconfont iconusercenter'></i> }
                        title="立即购买"
                        key="taobao"
                        onPress={ () => {
                            props.history.push('/dianpu')
                        } }
                    >      
			        </TabBar.Item>
                </TabBar>
        </div>
    )
}

export default Jump